import external from '../assets/icons/external.svg';

function DevelopMars() {
  const imageAlt = "last week on mars";
  const imageUrl = "https://res.cloudinary.com/gubcode/image/upload/v1615561876/colorPortfolio/develop/mars";
  const title = 'LAST WEEK ON MARS';
  const linkUrl = 'https://apichallenge2jh.web.app/';

  return (
    <div className="w-screen grid-cols-12 text-center tuffyBold tracking-widest">
      <picture class>
        <source media="(min-width:1280px)"
          srcset={`${imageUrl}-XL.png`} />
        <source media="(min-width:1024px)"
          srcset={`${imageUrl}-LG.png`} />
        <source media="(min-width:768px)"
          srcset={`${imageUrl}-MD.png`} />
        <source media="(min-width:640px)"
          srcset={`${imageUrl}-SM.png`} />
        <source media="(min-width:1px)"
          srcset={`${imageUrl}-XS.png`} />
        <img src={`${imageUrl}-MD.png`}
          alt={`${imageAlt}`}
          className="card" />
      </picture>
        <div className="text-3xl mt-5 pb-5 ml-5 mr-5">
          {title}
        </div>
        <div className="text-lg pb-5 ml-5 mr-5 tracking-wide">
          {/* description here */}
          MY SECOND API PROJECT AT EFA.<br/><br/>AN APP USED TO COMPARE THE MOST CURRENT MARS AND CORRESPONDING EARTH WEATHER DATA. UTILIZES METEOSTAT AND NASA'S INSIGHT APIS.
        </div>
        <div className="text-base tuffyItalic pb-5">
          {/* technologies */}
          - BOOTSTRAP
          <br/>
          - FIREBASE
          <br/>
        </div>
        {/* button */}
        <a href={linkUrl} target="_blank">
        <div className="text-base text-white bg-black border-black border-4 rounded-md ml-12 mr-12 pt-3 pb-3">
          {/* external link */}
          CHECK IT OUT<img className="external align-baseline ml-2" src={external} alt="external link"></img>
        </div>
        </a>
        <div className="p-4"></div>
    </div>
  )
}

export default DevelopMars;