import React  from 'react';
import Card from './Card';

function Design() {
  const projects = [
    // {number: "1", imageAlt: "test", title: "TEST PROJECT", page: "/design/test",
    // imageUrl: "https://res.cloudinary.com/gubcode/image/upload/v1615561876/colorPortfolio/design/test"},
    {number: "2", imageAlt: "portfolio3", title: "THIS PORTFOLIO", page: "/design/portfolio3",
    imageUrl: "https://res.cloudinary.com/gubcode/image/upload/v1615561876/colorPortfolio/design/portfolio3"},
    {number: "3", imageAlt: "portfolio2", title: "OLD PORTFOLIO", page: "/design/portfolio2",
    imageUrl: "https://res.cloudinary.com/gubcode/image/upload/v1615561876/colorPortfolio/design/portfolio2"},
    {number: "4", imageAlt: "porchswing", title: "PORCHSWING", page: "/design/porchswing",
    imageUrl: "https://res.cloudinary.com/gubcode/image/upload/v1615561876/colorPortfolio/design/porchswing"},
    {number: "5", imageAlt: "worthekik", title: "WORTHEKIK ELEKTRONIK", page: "/design/worthekik",
    imageUrl: "https://res.cloudinary.com/gubcode/image/upload/v1615561876/colorPortfolio/design/worthekik"},
  ];
  
  return (
    <div>
      <div className="w-screen">
      {
        projects.map((project) => 
        <Card key={project.number} imageAlt={project.imageAlt} title={project.title} page={project.page} imageUrl={project.imageUrl}/>
        )}
      </div>
    </div>
  )
};

export default Design;