import React from 'react';
import Card from './Card';

function Else() {
  const projects = [
      {number: "2", imageAlt: "resume", title: "RESUME", page: "/else/resume",
      imageUrl: "https://res.cloudinary.com/gubcode/image/upload/v1615561876/colorPortfolio/else/resume"},
      {number: "3", imageAlt: "gibbolune", title: "GIBBOLUNE", page: "/else/gibbolune",
      imageUrl: "https://res.cloudinary.com/gubcode/image/upload/v1615561876/colorPortfolio/else/gibbolune"},
      {number: "4", imageAlt: "hodgepodge", title: "HODGEPODGE", page: "/else/hodgepodge",
      imageUrl: "https://res.cloudinary.com/gubcode/image/upload/v1615561876/colorPortfolio/else/hodgepodge"},
      {number: "5", imageAlt: "contact", title: "CHAT WITH ME", page: "/else/contact",
      imageUrl: "https://res.cloudinary.com/gubcode/image/upload/v1615561876/colorPortfolio/else/contact"},
    ];
    
    return (
      <div>
        <div className="w-screen">
        {
          projects.map((project) => 
          <Card key={project.number} imageAlt={project.imageAlt} title={project.title} page={project.page} imageUrl={project.imageUrl} />
          )}
        </div>
      </div>
    )
  };
  
  export default Else;