import React from 'react';
import { Link } from 'react-router-dom';

function Card(props) {
  //passed in imageAlt, title, page, imageUrl

  // xs - 640	sm - 768	md - 1024	lg - 1280	xl - 1536

  return (
    <div className="text-center tuffyBold text-3xl tracking-widest">
      <Link to={props.page}>
        <picture>
          <source media="(min-width:1280px)" 
          srcset={`${props.imageUrl}-XL.png`}/>
          <source media="(min-width:1024px)" 
          srcset={`${props.imageUrl}-LG.png`}/>
          <source media="(min-width:768px)" 
          srcset={`${props.imageUrl}-MD.png`}/>
          <source media="(min-width:640px)" 
          srcset={`${props.imageUrl}-SM.png`}/>
          <source media="(min-width:1px)" 
          srcset={`${props.imageUrl}-XS.png`}/>
          <img src={`${props.imageUrl}-MD.png`}
          alt={`${props.imageAlt}`}
          className="card"/>
        </picture>
        <div className="mt-3 pb-16 ml-5 mr-5">
          {props.title}
        </div>
      </Link>
    </div >
  )
};

export default Card;