import external from '../assets/icons/external.svg';

function DevelopRecipeazy() {
  const imageAlt = "recipeazy";
  const imageUrl = "https://res.cloudinary.com/gubcode/image/upload/v1615561876/colorPortfolio/develop/recipeazy";
  const title = 'RECIPEAZY';
  const linkUrl = 'https://group-4-recipeazy-client.herokuapp.com/';

  return (
    <div className="w-screen grid-cols-12 text-center tuffyBold tracking-widest">
      <picture class>
        <source media="(min-width:1280px)"
          srcset={`${imageUrl}-XL.png`} />
        <source media="(min-width:1024px)"
          srcset={`${imageUrl}-LG.png`} />
        <source media="(min-width:768px)"
          srcset={`${imageUrl}-MD.png`} />
        <source media="(min-width:640px)"
          srcset={`${imageUrl}-SM.png`} />
        <source media="(min-width:1px)"
          srcset={`${imageUrl}-XS.png`} />
        <img src={`${imageUrl}-MD.png`}
          alt={`${imageAlt}`}
          className="card" />
      </picture>
        <div className="text-3xl mt-5 pb-5 ml-5 mr-5">
          {title}
        </div>
        <div className="text-lg pb-5 ml-5 mr-5 tracking-wide">
          {/* description here */}
          MY FIRST FULL-STACK PROJECT AT EFA - A WEEK-LONG GROUP EFFORT.<br/><br/>A PERN STACK SHOPPING LIST APP UTILIZING EDAMAME'S RECIPE SEARCH API. MY FIRST TIME WORKING WITH REACT AND USING TEAM GIT FOR VERSION CONTROL.<br/>*I APOLOGIZE IF MY HOBBYIST SERVER IS DOWN, I EAT THROUGH MY DYNOS PRETTY QUICKLY AND CANNOT INVEST MORE $ AT THIS POINT.
        </div>
        <div className="text-base tuffyItalic pb-5">
          {/* technologies */}
          - REACT JS
          <br/>
          - EXPRESS
          <br/>
          - NODE.JS
          <br/>
          - BCRYPT
          <br/>
          - JWT
          <br/>
          - POSTGRESQL
          <br/>
          - BOOTSTRAP
          <br/>
          - HEROKU
          <br/>
        </div>
        {/* button */}
        <a href={linkUrl} target="_blank">
        <div className="text-base text-white bg-black border-black border-4 rounded-md ml-12 mr-12 pt-3 pb-3">
          {/* external link */}
          CHECK IT OUT<img className="external align-baseline ml-2" src={external} alt="external link"></img>
        </div>
        </a>
        <div className="p-4"></div>
    </div>
  )
}

export default DevelopRecipeazy;